import React, { useState } from "react";
import { Button, Modal, Table } from "react-bootstrap";
import { MyComponentProps } from "../../../../../functions/DTO/CharacterInfo";
import { setShowStatModal } from "../../../../../store/actions";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../../../../../store/reducers";

function StatModal({ info }: MyComponentProps) {
  const showStatModal = useSelector((state: RootState) => state.showStatModal);
  const dispatch = useDispatch();

  const rankingItem = Array.isArray(info.unionRanking.ranking)
    ? info.unionRanking.ranking[0]
    : null;
  const characterName = rankingItem ? rankingItem.character_name : "";
  const addComma = (exp: number) => {
    let returnString = exp?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    return returnString;
  };

  const handleCloseStat = () => dispatch(setShowStatModal(false));
  const handleShowStat = () => dispatch(setShowStatModal(true));

  const AddUnitToBattlePower = (battlePower: number) => {
    let oek = Math.floor(battlePower / 100000000);
    let man = Math.floor((battlePower / 10000) % 10000);
    let result = "";
    if (oek > 0) {
      let stringman = man.toString().padStart(4, "0");
      result = `${oek}.${stringman}`;
    } else if (oek === 0 && man > 0) {
      result = `${man}`;
    } else {
      result = battlePower.toString();
    }
    return result;
  };

  const FindMainAndSubStat = (
    STR: string,
    DEX: string,
    INT: string,
    LUK: string,
    HP: string
  ) => {
    let stat = [0, 0, 0];
    let str = parseInt(STR);
    let dex = parseInt(DEX);
    let int = parseInt(INT);
    let luk = parseInt(LUK);
    let hp = parseInt(HP);
    let statsArray = [str, dex, int, luk].sort((a, b) => b - a);

    if (info.character_class === "") {
      stat = [hp, str, 0];
    } else if (
      info.character_class === "" ||
      info.character_class === "" ||
      info.character_class === "" ||
      info.character_class === ""
    ) {
      stat[0] = statsArray[0];
      stat[1] = statsArray[1];
      stat[2] = statsArray[2];
    } else {
      stat[0] = statsArray[0];
      stat[1] = statsArray[1];
      stat[2] = 0;
    }

    return stat;
  };
  function determineMainCharacter(searched: string, union: string) {
    if (searched === union) {
      return <></>;
    } else {
      return (
        <tr>
          <td>
            <strong> </strong>
          </td>
          <td>{union}</td>
        </tr>
      );
    }
  }
  const determineSecondSubStat = (stat: number) => {
    if (stat === 0) {
      return "";
    } else {
      return addComma(stat);
    }
  };
  return (
    <Modal show={showStatModal} onHide={handleCloseStat}>
      <Modal.Header closeButton>
        <Modal.Title>{info.character_name} </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Table bordered={false} hover={true}>
          <thead>
            <tr>
              <th>Stat</th>
              <th>Value</th>
            </tr>
          </thead>
          <tbody>
            {determineMainCharacter(info.character_name, characterName)}
            <tr>
              <td>
                <strong> </strong>
              </td>
              <td>
                <span>
                  {addComma(
                    parseInt(
                      (
                        info.final_stat?.find(
                          (stat: any) => stat.stat_name === ""
                        ) || { stat_value: 0 }
                      ).stat_value as string
                    )
                  ) || ""}
                </span>{" "}
                <span>
                  (
                  {AddUnitToBattlePower(
                    parseInt(
                      (
                        info.final_stat?.find(
                          (stat: any) => stat.stat_name === ""
                        ) || { stat_value: 0 }
                      ).stat_value as string
                    )
                  )}
                  )
                </span>
              </td>
            </tr>
            <tr>
              <td>
                <strong>%</strong>
              </td>
              <td>{info.character_exp_rate}%</td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>{addComma(info.character_exp)}</td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {addComma(
                  parseInt(
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === " "
                      ) || { stat_value: 0 }
                    ).stat_value as string
                  )
                ) || ""}{" "}
                (
                {AddUnitToBattlePower(
                  parseInt(
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === " "
                      ) || { stat_value: 0 }
                    ).stat_value as string
                  )
                )}
                )
              </td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {
                  (
                    info.final_stat?.find(
                      (stat: any) => stat.stat_name === ""
                    ) || { stat_value: 0 }
                  ).stat_value as string
                }
                %
              </td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {
                  (
                    info.final_stat?.find(
                      (stat: any) => stat.stat_name === "  "
                    ) || { stat_value: 0 }
                  ).stat_value as string
                }
                %
              </td>
            </tr>
            <tr>
              <td>
                <strong> </strong>
              </td>
              <td>
                {
                  (
                    info.final_stat?.find(
                      (stat: any) => stat.stat_name === " "
                    ) || { stat_value: 0 }
                  ).stat_value as string
                }
                %
              </td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {
                  (
                    info.final_stat?.find(
                      (stat: any) => stat.stat_name === " "
                    ) || { stat_value: 0 }
                  ).stat_value as string
                }
                %
              </td>
            </tr>

            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {
                  (
                    info.final_stat?.find(
                      (stat: any) => stat.stat_name === "  "
                    ) || { stat_value: 0 }
                  ).stat_value as string
                }
                %
              </td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {
                  (
                    info.final_stat?.find(
                      (stat: any) => stat.stat_name === ""
                    ) || { stat_value: 0 }
                  ).stat_value as string
                }
              </td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {addComma(
                  FindMainAndSubStat(
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "STR"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "DEX"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "INT"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "LUK"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "HP"
                      ) || { stat_value: 0 }
                    ).stat_value as string
                  )[0]
                )}
              </td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {addComma(
                  FindMainAndSubStat(
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "STR"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "DEX"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "INT"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "LUK"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "HP"
                      ) || { stat_value: 0 }
                    ).stat_value as string
                  )[1]
                )}
              </td>
            </tr>
            <tr>
              <td>
                <strong>2</strong>
              </td>
              <td>
                {determineSecondSubStat(
                  FindMainAndSubStat(
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "STR"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "DEX"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "INT"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "LUK"
                      ) || { stat_value: 0 }
                    ).stat_value as string,
                    (
                      info.final_stat?.find(
                        (stat: any) => stat.stat_name === "HP"
                      ) || { stat_value: 0 }
                    ).stat_value as string
                  )[2]
                )}
              </td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {
                  (
                    info.final_stat?.find(
                      (stat: any) => stat.stat_name === " "
                    ) || { stat_value: 0 }
                  ).stat_value as string
                }
                %
              </td>
            </tr>
            <tr>
              <td>
                <strong></strong>
              </td>
              <td>
                {
                  (
                    info.final_stat?.find(
                      (stat: any) => stat.stat_name === " "
                    ) || { stat_value: 0 }
                  ).stat_value as string
                }
                %
              </td>
            </tr>
            <tr>
              <td>
                <strong> row</strong>
              </td>
              <td> row</td>
            </tr>
          </tbody>
        </Table>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleCloseStat}>
          
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default StatModal;
