  function calculateAge(birthdate: Date, currentDate: Date): number {
    const msInYear = 1000 * 60 * 60 * 24 * 365.25;
    const ageInMillis = currentDate.getTime() - birthdate.getTime();
    return Math.floor(ageInMillis / msInYear);
  }

